.popup {
    font-family: 'Inter', Arial, sans-serif;
    margin: 0;
    padding: 0;
    opacity: 0;
    visibility: hidden;
    background-color: rgba(0, 0, 0, .5);
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    display: flex;
    transition: all 0.8s ease 0s;


}
